"use client"

import React from "react"
import { newsData, NewsItem } from "@/data/news"
import { useLanguage } from "@/components/LanguageProvider"
import { translations } from "@/data/translations"

type Props = {
  slug: string
}

function mergeBySlug(source: NewsItem[], translated: any[] | undefined) {
  if (!Array.isArray(translated) || translated.length === 0) return source
  return source.map((s) => {
    const match = translated.find((t) => t && t.slug && t.slug === s.slug)
    if (!match) return s
    return {
      ...s,
      title: match.title ?? s.title,
      summary: match.summary ?? s.summary,
      content: match.content ?? s.content,
      date: match.date ?? s.date
    }
  })
}

export default function NewsDetailClient({ slug }: Props) {
  const { lang } = useLanguage()
  const t = translations[lang] ?? translations.en
  const translatedNews = (t as any).news as any[] | undefined

  const merged = mergeBySlug(newsData, translatedNews)
  const item = merged.find((n) => n.slug === slug)

  if (!item) {
    return <div className="p-12 text-center">News not found.</div>
  }

  return (
    <article className="py-16">
      <div className="container mx-auto px-4 max-w-3xl">
        <h1 className="text-3xl font-serif font-bold text-stone-900 mb-2">{item.title}</h1>
        <p className="text-xs text-stone-400 mb-6">{item.date}</p>

        {/* content area */}
        <div className="prose max-w-none">
          <p>{item.content}</p>
        </div>
      </div>
    </article>
  )
}